.RollDice {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
}
.container {
  display: flex;
  justify-content: center;
  align-content: center;
}
.RollDice button {
  align-self: center;
  width: 15em;
  padding: 1.5em;
  border: 0px;
  border-radius: 20px;
  color: aliceblue;
  background-color: black;
  margin-top: 3em;
}

.RollDice button:hover {
  cursor: pointer;
  background-color: #af310a;
}
